.skills {
  background: #ffffff;
}

.skills-container {
  max-width: 1200px;
  margin: 0 auto;
}

.skills-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 35px;
  margin-bottom: 70px;
}

.skill-category {
  background: #ffffff;
  padding: 40px;
  border-radius: 16px;
  border: 1px solid rgba(37, 99, 235, 0.1);
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}

.skill-category::before {
  content: '';
  position: absolute;
  top: -2px;
  left: -2px;
  right: -2px;
  bottom: -2px;
  background: linear-gradient(135deg, rgba(37, 99, 235, 0.1), rgba(16, 185, 129, 0.1));
  border-radius: 16px;
  opacity: 0;
  transition: opacity 0.3s ease;
  z-index: -1;
}

.skill-category:hover {
  transform: translateY(-6px);
  box-shadow: 0 10px 30px rgba(37, 99, 235, 0.15);
  border-color: rgba(37, 99, 235, 0.3);
}

.skill-category:hover::before {
  opacity: 1;
}

.skill-category:hover::before {
  opacity: 0.15;
}

.category-title {
  font-size: 1.6rem;
  background: linear-gradient(135deg, #2563eb, #10b981);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  margin-bottom: 30px;
  text-align: center;
  font-weight: 700;
}

.skills-list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
  gap: 20px;
}

.skill-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 12px;
  padding: 20px 15px;
  background: #f8fafc;
  border-radius: 12px;
  border: 1px solid rgba(37, 99, 235, 0.1);
  transition: all 0.3s ease;
}

.skill-item:hover {
  background: rgba(37, 99, 235, 0.05);
  border-color: #2563eb;
  transform: scale(1.05);
  box-shadow: 0 8px 20px rgba(37, 99, 235, 0.15);
}

.skill-icon {
  font-size: 2.8rem;
  transition: transform 0.3s ease;
}

.skill-item:hover .skill-icon {
  transform: scale(1.15) rotateY(360deg);
}

.skill-name {
  color: #475569;
  font-size: 0.95rem;
  font-weight: 500;
  text-align: center;
}

.core-concepts {
  background: #ffffff;
  padding: 50px 45px;
  border-radius: 16px;
  border: 1px solid rgba(37, 99, 235, 0.1);
}

.core-concepts h3 {
  font-size: 2rem;
  background: linear-gradient(135deg, #2563eb, #10b981);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  margin-bottom: 30px;
  text-align: center;
  font-weight: 700;
}

.concepts-list {
  display: flex;
  flex-wrap: wrap;
  gap: 15px;
  justify-content: center;
}

.concept-tag {
  padding: 12px 24px;
  background: rgba(16, 185, 129, 0.08);
  color: #10b981;
  border: 1px solid rgba(16, 185, 129, 0.2);
  border-radius: 25px;
  font-size: 0.95rem;
  font-weight: 500;
  transition: all 0.3s ease;
  cursor: default;
}

.concept-tag:hover {
  background: rgba(16, 185, 129, 0.15);
  border-color: #10b981;
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(16, 185, 129, 0.2);
}

@media (max-width: 968px) {
  .skills-grid {
    grid-template-columns: 1fr;
    gap: 30px;
  }
}

@media (max-width: 768px) {
  .skills-list {
    grid-template-columns: repeat(2, 1fr);
  }

  .core-concepts {
    padding: 30px 20px;
  }
}
