.App {
  min-height: 100vh;
  background: transparent;
}

section {
  min-height: 100vh;
  padding: 100px 20px;
  position: relative;
}

.section-header {
  text-align: center;
  margin-bottom: 80px;
}

.section-title {
  font-size: 2.8rem;
  font-weight: 700;
  color: #1e293b;
  margin-bottom: 15px;
  position: relative;
  display: inline-block;
  letter-spacing: -0.5px;
}

.title-underline {
  width: 80px;
  height: 4px;
  background: linear-gradient(90deg, #2563eb, #10b981);
  margin: 20px auto;
  border-radius: 2px;
}

.section-subtitle {
  color: #64748b;
  font-size: 1.15rem;
  margin-top: 15px;
  font-weight: 400;
}

.btn {
  padding: 14px 35px;
  border-radius: 8px;
  font-size: 1rem;
  font-weight: 600;
  text-decoration: none;
  transition: all 0.3s ease;
  cursor: pointer;
  border: none;
  display: inline-block;
  letter-spacing: 0.5px;
  box-shadow: 0 2px 8px rgba(37, 99, 235, 0.2);
}

.btn-primary {
  background: linear-gradient(135deg, #2563eb, #1d4ed8);
  color: #ffffff;
  border: 2px solid transparent;
}

.btn-primary:hover {
  background: linear-gradient(135deg, #1d4ed8, #1e40af);
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(37, 99, 235, 0.3);
}

.btn-secondary {
  background: transparent;
  color: #2563eb;
  border: 2px solid #2563eb;
}

.btn-secondary:hover {
  background: #2563eb;
  color: #ffffff;
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(37, 99, 235, 0.3);
}

.btn-resume {
  background: linear-gradient(135deg, #10b981, #059669);
  color: #ffffff;
  border: 2px solid transparent;
  display: inline-flex;
  align-items: center;
  gap: 10px;
}

.btn-resume:hover {
  background: linear-gradient(135deg, #059669, #047857);
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(16, 185, 129, 0.3);
}

.highlight {
  color: #2563eb;
  font-weight: 600;
}

@media (max-width: 768px) {
  section {
    padding: 70px 15px;
  }

  .section-title {
    font-size: 2.2rem;
  }

  .section-subtitle {
    font-size: 1rem;
  }
}
