.footer {
  background: #1e293b;
  border-top: 2px solid rgba(37, 99, 235, 0.3);
  padding: 60px 20px 35px;
}

.footer-container {
  max-width: 1200px;
  margin: 0 auto;
}

.footer-content {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 50px;
  margin-bottom: 50px;
}

.footer-section h3 {
  font-size: 1.6rem;
  background: linear-gradient(135deg, #2563eb, #10b981);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  margin-bottom: 22px;
  font-weight: 700;
}

.footer-section h4 {
  font-size: 1.25rem;
  color: #cbd5e1;
  margin-bottom: 18px;
  font-weight: 600;
}

.footer-section p {
  color: #94a3b8;
  line-height: 1.7;
  font-size: 1.05rem;
}

.footer-links {
  list-style: none;
  padding: 0;
}

.footer-links li {
  margin-bottom: 12px;
}

.footer-links a {
  color: #94a3b8;
  text-decoration: none;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
  display: inline-block;
  font-size: 1.05rem;
}

.footer-links a::after {
  content: '';
  position: absolute;
  width: 0;
  height: 2px;
  bottom: -3px;
  left: 0;
  background: linear-gradient(90deg, #2563eb, #10b981);
  transition: width 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.footer-links a:hover {
  color: #2563eb;
}

.footer-links a:hover::after {
  width: 100%;
}

.footer-social {
  display: flex;
  gap: 18px;
}

.footer-social a {
  width: 50px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(37, 99, 235, 0.1);
  border: 2px solid rgba(37, 99, 235, 0.2);
  border-radius: 50%;
  color: #2563eb;
  font-size: 1.45rem;
  transition: all 0.3s ease;
}

.footer-social a:hover {
  background: linear-gradient(135deg, #2563eb, #10b981);
  border-color: transparent;
  color: #ffffff;
  transform: translateY(-3px);
  box-shadow: 0 4px 12px rgba(37, 99, 235, 0.3);
}

.footer-bottom {
  text-align: center;
  padding-top: 40px;
  border-top: 1px solid rgba(37, 99, 235, 0.2);
}

.footer-bottom p {
  color: #94a3b8;
  font-size: 1.05rem;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  flex-wrap: wrap;
  letter-spacing: 0.3px;
}

.heart-icon {
  color: #ef4444;
  animation: heartbeat 1.5s ease infinite;
}

@keyframes heartbeat {
  0%, 100% {
    transform: scale(1);
  }
  10%, 30% {
    transform: scale(1.1);
  }
  20%, 40% {
    transform: scale(1);
  }
}

@media (max-width: 768px) {
  .footer-content {
    grid-template-columns: 1fr;
    text-align: center;
  }

  .footer-social {
    justify-content: center;
  }

  .footer-links {
    text-align: center;
  }
}
