.projects {
  background: #f8fafc;
}

.projects-container {
  max-width: 1200px;
  margin: 0 auto;
}

.projects-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 40px;
}

.project-card {
  background: #ffffff;
  padding: 35px;
  border-radius: 16px;
  border: 1px solid rgba(37, 99, 235, 0.1);
  transition: all 0.3s ease;
  display: flex;
  flex-direction: column;
  position: relative;
  overflow: hidden;
}

.project-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: linear-gradient(90deg, #2563eb, #10b981);
  transform: scaleX(0);
  transition: transform 0.3s ease;
}

.project-card:hover {
  transform: translateY(-10px);
  box-shadow: 0 15px 40px rgba(37, 99, 235, 0.15);
  border-color: rgba(37, 99, 235, 0.3);
}

.project-card:hover::before {
  transform: scaleX(1);
}

.project-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 20px;
}

.project-title {
  font-size: 1.6rem;
  color: #1e293b;
  flex: 1;
  font-weight: 700;
}

.project-links {
  display: flex;
  gap: 15px;
}

.project-links a {
  color: #475569;
  font-size: 1.5rem;
  transition: all 0.3s ease;
  padding: 8px;
  border-radius: 8px;
  background: #f8fafc;
}

.project-links a:hover {
  color: #2563eb;
  background: rgba(37, 99, 235, 0.1);
  transform: translateY(-2px);
}

.project-description {
  color: #475569;
  line-height: 1.7;
  margin-bottom: 25px;
  font-size: 1.05rem;
}

.project-features {
  margin-bottom: 25px;
}

.project-features h4 {
  color: #1e293b;
  margin-bottom: 12px;
  font-size: 1.05rem;
  font-weight: 600;
}

.project-features ul {
  list-style: none;
  padding-left: 0;
}

.project-features li {
  color: #475569;
  padding: 8px 0;
  position: relative;
  padding-left: 25px;
  line-height: 1.6;
}

.project-features li::before {
  content: '▹';
  position: absolute;
  left: 0;
  color: #10b981;
  font-size: 1.3rem;
}

.project-tech {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  margin-top: auto;
  padding-top: 25px;
}

.tech-tag {
  padding: 8px 18px;
  background: rgba(37, 99, 235, 0.08);
  color: #2563eb;
  border: 1px solid rgba(37, 99, 235, 0.2);
  border-radius: 20px;
  font-size: 0.85rem;
  font-weight: 500;
  transition: all 0.3s ease;
}

.tech-tag:hover {
  background: rgba(37, 99, 235, 0.15);
  border-color: #2563eb;
  transform: translateY(-2px);
}

@media (max-width: 768px) {
  .projects-grid {
    grid-template-columns: 1fr;
  }

  .project-card {
    padding: 25px;
  }
}
