.education {
  background: #ffffff;
}

.education-container {
  max-width: 1000px;
  margin: 0 auto;
}

.education-timeline {
  display: flex;
  flex-direction: column;
  gap: 40px;
  padding: 20px 0;
}

.education-card {
  background: #ffffff;
  padding: 40px;
  border-radius: 16px;
  border: 1px solid rgba(37, 99, 235, 0.1);
  position: relative;
  transition: all 0.3s ease;
}

.education-card:hover {
  transform: translateX(-8px);
  box-shadow: 0 10px 30px rgba(37, 99, 235, 0.15);
  border-color: rgba(37, 99, 235, 0.3);
}

.education-icon {
  position: absolute;
  top: 40px;
  right: -65px;
  width: 55px;
  height: 55px;
  background: linear-gradient(135deg, #2563eb, #10b981);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #ffffff;
  font-size: 1.6rem;
  box-shadow: 0 4px 12px rgba(37, 99, 235, 0.3);
  border: 3px solid #ffffff;
}

.education-content {
  margin-right: 20px;
}

.education-degree {
  font-size: 1.7rem;
  color: #1e293b;
  margin-bottom: 12px;
  font-weight: 700;
}

.education-institution {
  font-size: 1.25rem;
  color: #2563eb;
  margin-bottom: 25px;
  font-weight: 500;
}

.university {
  color: #475569;
  font-size: 1.05rem;
  font-weight: 600;
}

.education-meta {
  display: flex;
  flex-wrap: wrap;
  gap: 30px;
  margin-bottom: 20px;
}

.meta-item {
  display: flex;
  align-items: center;
  gap: 10px;
  color: #475569;
  font-size: 1rem;
  padding: 10px 18px;
  background: #f8fafc;
  border-radius: 8px;
  border: 1px solid rgba(37, 99, 235, 0.1);
}

.meta-item svg {
  color: #2563eb;
  font-size: 1.15rem;
}

.education-status {
  color: #10b981;
  font-style: italic;
  margin: 18px 0;
  font-size: 1.05rem;
  font-weight: 500;
}

.education-highlights {
  margin-top: 25px;
}

.education-highlights h5 {
  color: #1e293b;
  margin-bottom: 15px;
  font-size: 1.1rem;
  font-weight: 600;
}

.education-highlights ul {
  list-style: none;
  padding: 0;
}

.education-highlights li {
  color: #475569;
  padding: 8px 0;
  position: relative;
  padding-left: 30px;
  line-height: 1.7;
}

.education-highlights li::before {
  content: '▹';
  position: absolute;
  left: 0;
  color: #10b981;
  font-size: 1.4rem;
}

@media (max-width: 768px) {
  .education-icon {
    position: relative;
    right: 0;
    margin-bottom: 20px;
  }

  .education-content {
    margin-right: 0;
  }

  .education-degree {
    font-size: 1.3rem;
  }

  .education-institution {
    font-size: 1rem;
  }

  .education-meta {
    flex-direction: column;
    gap: 15px;
  }
}
