.about {
  background: #ffffff;
}

.about-container {
  max-width: 1200px;
  margin: 0 auto;
}

.about-content {
  display: flex;
  gap: 50px;
  align-items: center;
}

.about-text {
  flex: 1;
}

.about-text p {
  color: #475569;
  font-size: 1.1rem;
  line-height: 1.9;
  margin-bottom: 25px;
}

.about-highlights {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 30px;
  margin-top: 60px;
}

.highlight-item {
  text-align: center;
  padding: 40px 25px;
  background: #ffffff;
  border-radius: 16px;
  border: 2px solid rgba(37, 99, 235, 0.1);
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}

.highlight-item::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: linear-gradient(90deg, #2563eb, #10b981);
  transform: scaleX(0);
  transition: transform 0.3s ease;
}

.highlight-item:hover {
  transform: translateY(-6px);
  box-shadow: 0 10px 30px rgba(37, 99, 235, 0.15);
  border-color: rgba(37, 99, 235, 0.3);
}

.highlight-item:hover::before {
  transform: scaleX(1);
}

.highlight-item h3 {
  font-size: 2.5rem;
  background: linear-gradient(135deg, #2563eb, #10b981);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  margin-bottom: 10px;
  font-weight: 700;
}

.highlight-item p {
  color: #64748b;
  font-size: 1.05rem;
  margin: 0;
  font-weight: 500;
}

@media (max-width: 968px) {
  .about-content {
    flex-direction: column;
  }
}

@media (max-width: 768px) {
  .about-highlights {
    grid-template-columns: 1fr;
    gap: 20px;
  }

  .about-text p {
    font-size: 1rem;
  }
}
