.hero {
  min-height: 100vh;
  display: flex;
  align-items: center;
  background: #ffffff;
  padding-top: 75px;
  position: relative;
  overflow: hidden;
}

.hero::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: transparent;
  pointer-events: none;
}

.hero-container {
  max-width: 1200px;
  margin: 0 auto;
  width: 100%;
  position: relative;
  z-index: 1;
}

.hero-content {
  animation: fadeInUp 1s ease;
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.hero-greeting {
  color: #10b981;
  font-size: 1.1rem;
  margin-bottom: 25px;
  font-weight: 500;
  letter-spacing: 1px;
  text-transform: uppercase;
}

.hero-name {
  font-size: 4.5rem;
  font-weight: 800;
  color: #1e293b;
  margin-bottom: 15px;
  line-height: 1.1;
  letter-spacing: -2px;
}

.hero-title {
  font-size: 3.2rem;
  font-weight: 700;
  background: linear-gradient(135deg, #2563eb, #10b981);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  margin-bottom: 35px;
  letter-spacing: -1px;
}

.hero-description {
  max-width: 650px;
  color: #475569;
  font-size: 1.15rem;
  line-height: 1.8;
  margin-bottom: 45px;
  font-weight: 400;
}

.hero-info {
  display: flex;
  flex-wrap: wrap;
  gap: 35px;
  margin-bottom: 45px;
}

.info-item {
  display: flex;
  align-items: center;
  gap: 12px;
  color: #475569;
  font-size: 1rem;
  padding: 12px 20px;
  background: #f8fafc;
  border-radius: 8px;
  border: 1px solid rgba(37, 99, 235, 0.1);
  transition: all 0.3s ease;
}

.info-item:hover {
  background: rgba(37, 99, 235, 0.05);
  border-color: #2563eb;
  transform: translateY(-2px);
}

.info-item svg {
  color: #2563eb;
  font-size: 1.2rem;
}

.hero-buttons {
  display: flex;
  gap: 20px;
  margin-bottom: 50px;
  flex-wrap: wrap;
}

.hero-social {
  display: flex;
  gap: 20px;
  margin-top: 50px;
}

.hero-social a {
  width: 50px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #ffffff;
  border: 2px solid rgba(37, 99, 235, 0.2);
  border-radius: 50%;
  color: #2563eb;
  font-size: 1.5rem;
  transition: all 0.3s ease;
}

.hero-social a:hover {
  background: linear-gradient(135deg, #2563eb, #10b981);
  border-color: transparent;
  color: #ffffff;
  transform: translateY(-3px);
  box-shadow: 0 6px 20px rgba(37, 99, 235, 0.3);
}

@media (max-width: 768px) {
  .hero-name {
    font-size: 2.5rem;
  }

  .hero-title {
    font-size: 2rem;
  }

  .hero-description {
    font-size: 1rem;
  }

  .hero-info {
    flex-direction: column;
    gap: 15px;
  }

  .hero-buttons {
    flex-direction: column;
  }

  .btn {
    text-align: center;
  }
}

@media (max-width: 480px) {
  .hero-name {
    font-size: 2rem;
  }

  .hero-title {
    font-size: 1.5rem;
  }
}
