.navbar {
  position: fixed;
  top: 0;
  width: 100%;
  height: 75px;
  display: flex;
  align-items: center;
  padding: 0 50px;
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(20px);
  border-bottom: 1px solid rgba(37, 99, 235, 0.1);
  z-index: 1000;
  transition: all 0.3s ease;
}

.navbar-scrolled {
  box-shadow: 0 4px 20px rgba(37, 99, 235, 0.1);
  background: rgba(255, 255, 255, 0.98);
  border-bottom: 1px solid rgba(37, 99, 235, 0.2);
}

.navbar-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  max-width: 1400px;
  margin: 0 auto;
}

.navbar-logo {
  font-size: 1.6rem;
  font-weight: 700;
  background: linear-gradient(135deg, #2563eb, #10b981);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  cursor: pointer;
  transition: all 0.3s ease;
  letter-spacing: -0.5px;
}

.navbar-logo:hover {
  transform: scale(1.05);
}

.navbar-menu {
  display: flex;
  list-style: none;
  gap: 45px;
}

.navbar-menu li a {
  color: #475569;
  text-decoration: none;
  font-size: 0.95rem;
  font-weight: 500;
  transition: all 0.3s ease;
  position: relative;
  padding: 8px 0;
  letter-spacing: 0.5px;
}

.navbar-menu li a::after {
  content: '';
  position: absolute;
  width: 0;
  height: 2px;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  background: linear-gradient(90deg, #2563eb, #10b981);
  transition: width 0.3s ease;
}

.navbar-menu li a:hover {
  color: #2563eb;
}

.navbar-menu li a:hover::after {
  width: 100%;
}

.hamburger {
  display: none;
  cursor: pointer;
  color: #2563eb;
  font-size: 1.6rem;
  z-index: 1001;
  transition: transform 0.3s ease;
}

.hamburger:hover {
  transform: scale(1.1);
}

.mobile-menu {
  display: none;
  flex-direction: column;
  position: fixed;
  top: 0;
  right: -100%;
  width: 75%;
  max-width: 300px;
  height: 100vh;
  background: #ffffff;
  padding: 100px 30px 30px;
  list-style: none;
  transition: right 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  box-shadow: -10px 0 30px rgba(37, 99, 235, 0.15);
  border-left: 2px solid rgba(37, 99, 235, 0.2);
}

.mobile-menu.active {
  right: 0;
}

.mobile-menu li {
  margin: 25px 0;
  text-align: center;
  opacity: 0;
  animation: fadeInUp 0.5s forwards;
}

.mobile-menu li:nth-child(1) { animation-delay: 0.1s; }
.mobile-menu li:nth-child(2) { animation-delay: 0.2s; }
.mobile-menu li:nth-child(3) { animation-delay: 0.3s; }
.mobile-menu li:nth-child(4) { animation-delay: 0.4s; }
.mobile-menu li:nth-child(5) { animation-delay: 0.5s; }
.mobile-menu li:nth-child(6) { animation-delay: 0.6s; }
.mobile-menu li:nth-child(7) { animation-delay: 0.7s; }

@keyframes fadeInUp {
  to {
    opacity: 1;
    transform: translateY(0);
  }
  from {
    opacity: 0;
    transform: translateY(20px);
  }
}

.mobile-menu li a {
  color: #475569;
  text-decoration: none;
  font-size: 1.1rem;
  font-weight: 500;
  transition: all 0.3s ease;
  display: block;
  padding: 12px;
  border-radius: 8px;
}

.mobile-menu li a:hover {
  color: #2563eb;
  background: rgba(37, 99, 235, 0.1);
  transform: translateX(5px);
}

@media (max-width: 768px) {
  .navbar {
    padding: 0 20px;
    height: 70px;
  }

  .navbar-menu {
    display: none;
  }

  .hamburger {
    display: block;
  }

  .mobile-menu {
    display: flex;
  }
}
